<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
import BaseFormTextVue from '@/components/BaseFormText.vue';
import BaseFormTextAreaVue from '@/components/BaseFormTextArea.vue';
import numberFormat from '@/helpers/numberFormat';
import OrderItemVue from '@/components/OrderItem.vue';
import API_BASE_URL from '../config';

export default {
  components: { BaseFormTextVue, BaseFormTextAreaVue, OrderItemVue },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
    };
  },
  filters: { numberFormat },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      axios
        .post(
          `${API_BASE_URL}/api/orders`,
          {
            ...this.formData,
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          },
        )
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          // почему через мутацию из компонента а не через действие а потом мутацию, вроде же нельзя
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.message;
        });
    },
  },
  computed: {
    ...mapGetters({
      productsCalc: 'cartDetailProducts',
      productsSumma: 'cartTotalPrice',
      cartLength: 'cartLength',
    }),
  },
};
</script>
